//
// Topbar Mailbox
//
$mailbox-width: 300px;
$mailbox-height: 317px;

/*******************
/*User mail widgets*/
/*******************/
.mailbox .chat-scroll {
  height: calc(100vh - 100px) !important;
}

.mailbox {
  .message-center {
    height: $mailbox-height;
    overflow: auto;
    .message-item {
      display: flex;
      &:hover,
      &.active {
        // background: rgba(0, 0, 0, 0.025);
        background: rgba($primary-tonal, 0.3);
      }
      // .message-title {
      //   color: $gray-900;
      // }
      .user-img {
        width: 45px;
        .profile-status {
          border: 2px solid $white;
          height: 10px;
          left: 30px;
          position: absolute;
          top: 1px;
          width: 10px;
        }
        .online {
          background: $success;
        }
        .busy {
          background: $danger;
        }
        .away {
          background: $warning;
        }
        .offline {
          background: $warning;
        }
      }
    }
  }
}

// shooping cart dropdown
.shopping-widget {
  position: relative;
  max-width: 100px;
  .decrease-btn {
    position: absolute;
    top: 5px;
    left: 5px;
  }
  .increase-btn {
    position: absolute;
    top: 5px;
    right: 5px;
  }
}
