// ###############################################
// Login Tekprovider v1
// ###############################################
:root {
    //Demo Tekprovider
    --login_bg: url(../../assets/images/login/bg-tek.jpg) no-repeat;
    --login_bg_mobile: url(../../assets/images/login/bg-tek-h.jpg) no-repeat;
    --primary_login_color: #1a9bfc;
    --primary_login_color_highlight: #1884d6;
    --secondary_login_color: #eeeeee;
    --secondary_login_color_highlight: #eeeeee;
    --bg_color: #fafafa;

    //Demo Santander
    // --login_bg: url(../../assets/images/login/bg-tek.jpg) no-repeat;
    // --login_bg_mobile: url(../../assets/images/login/bg-tek-h.jpg) no-repeat;
    // --primary_login_color: #ec0000;
    // --primary_login_color_highlight: #b81616;
    // --secondary_login_color: #eeeeee;
    // --secondary_login_color_highlight: #eeeeee;
    // --bg_color: #f2f2f2;

}


.dynamic-ui-login {
    
    .bg-dynamic-class {
        background: var(--login_bg);
        background-position: top center;
        background-size: cover;
    }

    .pre-header-holder {
        display: flex;
        justify-content: space-between;
        width: 100%;

        .img-logo-logo {
            margin-bottom: 30px;
    
            img {
                width: 100%;
                max-width: 150px;
            }
    
            &.small-logo{
                img {
                    width: 100%;
                    max-width: 120px;
                }
            }
    
            &.medium-logo{
                img {
                    width: 100%;
                    max-width: 180px;
                }
            }
    
            &.big-logo{
                img {
                    width: 100%;
                    max-width: 200px;
                }
            }
    
            &.large-logo{
                img {
                    width: 100%;
                    max-width: 230px;
                }
            }
        }
    }



    .form-card {
        .lang-options {
            width: 90px;
            display: flex;
            background: #fafafa;
            border-radius: 10px;
            border: solid 1px #eee;
            height: min-content;
            align-items: center;
            justify-content: center;
            right: 30px;
            top: -20px;
            position: absolute;

            a {
                width: 40px;
                padding: 6px;

                img {
                    max-width: 36px;
                }
            }
        }

        .btn-primary {
            background: var(--primary_login_color);
            background-color: var(--primary_login_color);
            border-color: var(--primary_login_color);

            &:hover {
                background: var(--primary_login_color_highlight);
                background-color: var(--primary_login_color_highlight);
                border-color: var(--primary_login_color);
            }
        }
    }

    .login-form-holder {
        background: var(--bg_color);
    }

    .extra-info-holder {
        display: flex;
        flex-direction: column;
        align-items: baseline;
        justify-content: space-between;
        width: 100%;

        .extra-info {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;

            .rights {
                font-size: 13px;
                text-align: left;
            }
            .origin {
                font-size: 12px;
                text-align: right;
            }
        }
    }

}

@media only screen and (max-width: 991px) {
    .dynamic-ui-login {
    
        .bg-dynamic-class {
            background: var(--login_bg_mobile);
            background-position: center left;
            background-size: cover;
        }
    
        .extra-info-holder {
            .extra-info {
                flex-direction: column;
                align-items: center;
                justify-content: center;
    
                .rights {
                    margin-bottom: 20px;
                    text-align: center;
                }
                .origin {
                    text-align: center;
                }
            }
        }
    }
}

@media only screen and (max-width: 767px) {
    .dynamic-ui-login {
    
        .form-card {
            .lang-options {
                width: 74px;
                top: -68px;
    
                a {
                    width: 30px;
                    padding: 3px;
    
                    img {
                        max-width: 26px;
                    }
                }
            }
        }

        .extra-info-holder {
            .extra-info {
                .origin {
                    display: none;
                }
            }
        }
    }
}