//
// Master file to tek adjustments and overwriting existing styles
//

.ui-loader {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    background: rgba(255,255,255,0.95);
    display: flex;
    align-items: center;
    justify-content: center;

    img {
        width: 100%;
        max-width: 200px;
    }

    &.loader-big {
        img {
            width: 100%;
            max-width: 300px;
        }
    }
}

.logo-sidebar {
    width: 100%;
    max-width: 52px;
}

.logo-text-sidebar {
    width: 100%;
    max-width: 152px;
}

.breadcrumb-holder{
    ol {
        --bs-breadcrumb-margin-bottom: 0rem !important;
    }
}

.bg-light-grey {
    background: $light-grey-bg !important;
}

.bg-extra-light-grey {
    background: $extra-light-grey-bg !important;
}

.bg-container-main {
    background: $bg-containter-main !important;
}

.bg-primary-tonal {
    background: $primary-tonal !important;
}

.btn-light-tonal {
    background: $primary-tonal !important;
    background-color: $primary-tonal !important;
}

.navbar-boxed-icon {
    // background: $extra-light-grey-bg !important;
    background: #f7f7f7 !important;
    width: 45px !important;
    height: 45px !important;
    min-width: 45px !important;
    min-height: 45px !important;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
}

.close-profile {
    position: absolute;
    right: 10px;
    top: 10px;
}

.tab-drawer-menu {
    margin-left: -26px;
    margin-right: -26px;
    margin-top: -25px;
    margin-bottom: 10px;
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    position: relative;
    will-change: transform;

    &.active {
        cursor: grabbing;
        cursor: -webkit-grabbing;
    }

    &::-webkit-scrollbar {
        width: 10px;
        height: 10px;
        // background: transparent;
    }

    &::-webkit-scrollbar-thumb {
        // background: #dddddd;
        background: transparent;
        border-radius: 6px;
    }

    &::-webkit-scrollbar-track {
        background: transparent;
    }

    &:hover {
        &::-webkit-scrollbar-thumb {
            background: #dddddd;
        }
    }

    .card {
        border-radius: 0px 0px 0px 0px !important;
        box-shadow: none;
        text-decoration: none;
        color: inherit;
        width: auto;
        max-width: 270px;
        min-width: 270px;
        border-right-color: transparent;
        // border: solid 1px $elements-grey-border !important;

        &:hover {
            border-top: solid 1px $elements-grey-border !important;
            border-bottom: solid 1px $elements-grey-border !important;
        }

        h5 {
            color: $primary;
        }

        &.current-drawer-step {
            background: $primary-tonal-min;

            h5 {
                font-weight: 600;
            }

            svg {
                color: $primary;
            }

            i {
                color: $primary;
            }
        }

        &.active {
            background: $primary-tonal-min;

            h5 {
                font-weight: 600;
            }

            svg {
                color: $primary;
            }

            i {
                color: $primary;
            }
        }

        &.completed-drawer-step {
            position: relative;

            &::after {
                content: "";
                width: 100%;
                margin: auto;
                height: 4px;
                background-color: $success;
                position: absolute;
                bottom: 0px !important;
                border-radius: 10px 0px 0px 10px;
            }
        }
    }
}

.container-fluid {
    &.no-width-restrictions {
        max-width: none !important;

        .page-titles {
            max-width: none !important;
        }
    }
}

.full-height {
    height: 100%;
}

.bg-outline-primary {
    background: $white;
    color: $primary;

    &:hover {
        background: $primary !important;
        color: $white !important;
    }
}

.ui-title-action-bar {
    // background: rgba(255,255,255,0.8);
    // background: $primary-tonal;
    background: $primary-tonal-min;
    margin-bottom: -2px;
    padding: 10px 10px 10px 20px;
    border-radius: 5px 5px 0px 0px;
    // border-left: solid 2px $primary !important;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;

    &::before {
        content: "";
        width: 6px;
        height: 55px;
        background-color: $primary;
        position: absolute;
        top: 0px;
        left: -5px;
        border-radius: 10px 0px 0px 10px;
    }

    .ui-title {
        width: 100%;
        max-width: 350px;
        h4 {
            padding-bottom: 0;
            margin-bottom: 0;
        }
        p {
            padding-bottom: 0;
            margin-bottom: 0;
            font-size: 13px;
        }
    }

    .ui-action-bar {
        display: flex;
        margin-left: auto;
        gap: 10px;
        // max-width: 350px;
    }

    &.action-inner-modules {
        border-radius: 5px 5px 5px 5px;

        &::before {
            content: "";
            width: 6px;
            height: 42px;
            background-color: $primary;
            position: absolute;
            top: 0px;
            left: -5px;
            border-radius: 10px 0px 0px 10px;
        }
    }
}

.ui-select {
    position: relative;

    label {
        font-weight: 500;
        // margin-bottom: 5px;
        font-size: 15px;
        position: absolute;
        // top: 10px;
        top: 8px;
        left: 12px;
        width: 100%;
        height: 100%;
        padding: 0px 0px;
        overflow: hidden;
        text-align: start;
        text-overflow: ellipsis;
        white-space: nowrap;
        pointer-events: none;
        transform-origin: 0 0;
        transition: opacity 0.1s ease-in-out, transform 0.1s ease-in-out;
        opacity: 0.65;
        transform: scale(0.85);
        // color: $black;
    }

    select {
        height: calc(3.5rem + 2px);
        padding: 25px 36px 8px 12px !important;
    }
}

.required-tag{
    font-weight: bold;
    color: $danger !important;
    font-size: 17px !important;
}

button.addon-btn-attached-right {
    border-radius: 0px 5px 5px 0px !important;
}

button.addon-btn-attached-left {
    border-radius: 5px 0px 0px 5px !important;
}

.offcanvas-ui{
    background: $primary-tonal;
}

.offcanvas-medium {
    width: 100% !important;
    max-width: 550px !important;
}

.offcanvas-large {
    width: 100% !important;
    max-width: 850px !important;
}

.offcanvas-xl {
    width: 100% !important;
    max-width: 1200px !important;
}

.offcanvas-full-size {
    width: 100% !important;
    max-width: 100% !important;
}

header {
    &.topbar {
        border-top: solid 1px #e6e9eb;
    }
}

.table-ui-mod {
    .dataTable {
        thead {
            tr {
                th {
                    padding: 10px !important;
                    font-weight: 700;
                    width: 60px !important;
                    min-width: 120px !important;
                    
                    &.sorting_asc {
                        &:before {
                            bottom: 0.6em !important;
                            right: 1.3em !important;
                        }
                        &:after {
                            bottom: 0.6em !important;
                        }
                    }
                    
                    &.sorting {
                        &:before {
                            bottom: 0.6em !important;
                            right: 1.3em !important;
                        }
                        &:after {
                            bottom: 0.6em !important;
                        }
                    }

                    &.streech-th {
                        padding: 0px !important;
                        margin: 0px !important;
                        width: 10px !important;
                        min-width: 10px !important;
                        max-width: 10px !important;

                        &:before {
                            display: none !important;
                        }
                        &:after {
                            display: none !important;
                        }
                    }
                }
            }
        }
        tbody {
            tr {
                th {
                    padding: 4px 12px !important;

                    &.streech-td {
                        padding-right: 5px !important;
                        margin: 0px !important;
                        width: 10px !important;
                        min-width: 10px !important;
                        max-width: 10px !important;
                    }
                }
                td {
                    padding: 4px 12px !important;

                    &.streech-td {
                        padding-right: 20px !important;
                        margin: 0px !important;
                        width: 10px !important;
                        min-width: 10px !important;
                        max-width: 10px !important;
                    }
                }
            }
        }
    }
}

.datepicker {
    thead {
        tr {
            th {
                text-align: center !important;
            }
        }
    }
}

.pointer {
    cursor: pointer !important;
}

.row.form-compact-holder > .col-12 {
    padding-right: 0px !important;
}

.row.form-compact-holder {
    padding-right: 15px !important;
}

.small-icon-btn {
    padding: 5px 8px !important;
}

.dropdown-item.active, .dropdown-item:active {
    color: $black;
}

@media only screen and (max-width: 767px) {
    .container-fluid {
        &.no-width-restrictions {
            padding: 15px;
        }
    }
    .topbar .navbar-collapse {
        padding: 0px 0px 0 0 !important;
    }
    .page-titles {
        margin-left: -15px;
        margin-right: -15px;
    }
}
@media only screen and (max-width: 600px) {
    .fixed-on-mobile {
        top: 0;
    }
    .navbar-mobile-structure {
        padding-top: 20px !important;
        display: flex;
        flex-direction: column;
        box-shadow: -1px 0px 18px -7px rgba(0,0,0,0.25);
        -webkit-box-shadow: -1px 0px 18px -7px rgba(0,0,0,0.25);
        -moz-box-shadow: -1px 0px 18px -7px rgba(0,0,0,0.25);

        .breadcrumb-holder {
            margin: auto;
            background: $light-grey-bg !important;
            width: 100%;
            margin-left: 0px !important;
        }
    }
    .ui-title-action-bar {
        flex-direction: column;
        align-items: flex-start;

        .ui-action-bar {
            margin-left: unset;
            margin-top: 10px;

            .dropdown {
                margin-left: 0px !important;
            }
        }
    }
}
.nav-pills {
    .nav-link{
        &.active {
            background-color: $primary !important;
        }
    }
    .show > .nav-link {
        background-color: $primary !important;
    }
}
.cliente-data-box { 
    .cliente-header{
        display: flex;
        flex-direction: column;
        align-items: start;

        .logo-holder {
            border: solid 1px $light-grey-bg !important;
            padding: 10px;
            border-radius: 8px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 20px;

            .client-logo {
                width: 100%;
                max-width: 120px;
                display: block;
            }
        }
        .client-info {
            width: 100%;

            .client-name {
                font-weight: 600;
                font-size: 18px;
                color: $black;
            }
            .client-data-row {
                text-decoration: none;
                color: inherit;

                &.link {
                    &:hover {
                        color: $primary;
                    }
                }

                &.client-manager {

                }
            }
            .client-btn-group {
                padding-top: 25px;
            }
        }
    }
}
.sticky-top-card{
    position: sticky;
    top: 100px;
}
#main-wrapper {
    overflow: visible !important;
}
.score-num {
    font-size: 45px;
}
.icon-score {
    font-size: 45px !important;
}
.black-list-table {
    .table-striped > tbody > tr > * {
        --bs-table-accent-bg: rgba(0,0,0,0.1) !important;
    }
}
.table-align-middle {
