//
// Calendar Widget
//

.calendar {
  margin-bottom: 0;
}

.fc-view {
  margin-top: 30px;
}

.none-border {
  .modal-footer {
    border-top: none;
  }
}

.fc-toolbar {
  margin-bottom: 5px;
  margin-top: 15px;
  h2 {
    font-size: 18px;
    font-weight: 500;
    line-height: 30px;
    text-transform: uppercase;
  }
}

.fc-day {
  background: $white;
}

.fc-toolbar .fc-state-active,
.fc-toolbar .ui-state-active,
.fc-toolbar button:focus,
.fc-toolbar button:hover,
.fc-toolbar .ui-state-hover {
  z-index: 0;
}

.fc-widget-header {
  border: 0 !important;
}

.fc-widget-content {
  border-color: rgba(120, 130, 140, 0.13) !important;
}

.fc {
  th.fc-widget-header {
    color: $body-color;
    font-size: 15px;
    line-height: 20px;
    font-weight: 500;
    padding: 7px 0;
    text-align: center;
  }
}

.fc-view {
  margin-top: 0;
}

.fc-toolbar {
  margin: 0;
  padding: 24px 0;
}

.fc-button {
  background: $white;
  border: 1px solid $border-color;
  color: $body-color;
  text-transform: capitalize;
  &:hover {
    background: $light;
    opacity: 0.8;
  }
}

.fc-text-arrow {
  font-size: 16px;
  font-family: inherit;
}

.fc-state-hover {
  background: #f5f5f5;
}

.fc-unthemed .fc-today {
  border: 1px solid $danger;
  background: $light !important;
}

.fc-state-highlight,
.fc-cell-overlay {
  background: #f0f0f0;
}

.fc-unthemed {
  .fc-today {
    background: $white;
  }
}

