.fc-event {
  border-radius: 0;
  border: none;
  background: $light-primary;
  color: $body-color !important;
  padding: 5px 5px;
  margin: 1px -1px 0 -1px;
  cursor: move;
  font-size: 13px;
  text-align: center;
}

.calendar-event {
  margin: 10px 5px 0 0;
  padding: 6px 10px;
  display: inline-block;
  cursor: move;
  color: $white;
  text-align: center;
  min-width: 140px;
  background: $primary;
  a {
    opacity: 0.6;
    font-size: 10px;
    color: $white;
    margin: 4px 0 0 10px;
  }
}

.fc-basic-view {
  td.fc-week-number {
    span {
      padding-right: 5px;
    }
  }
  .fc-day-number {
    display: inline-block;
    padding: 10px 15px;
  }
}
.fc-state-default {
  text-shadow: none;
  box-shadow: none;
}

.fc-basic-view .fc-body .fc-row {
  min-height: 5rem;
}

.fc-toolbar .fc-button-group {
  border: 1px solid $light-secondary;
  border-radius: 4px;
}

.fc-toolbar .fc-button {
  background: $light-secondary;
  color: $body-color;
  border: none;
  color: $gray-600;
}

.fc-toolbar .fc-button:hover {
  background: $light-secondary;
  opacity: 1;
}

.fc-toolbar .fc-button.fc-state-active {
  background: $white;
  color: $gray-600;
}

.fc-widget-header {
  border: 0px !important;
}

.fc-widget-content {
  border-color: rgba(120, 130, 140, 0.13) !important;
}

.fc-widget-content tr {
  border-bottom: none;
}

.fc-view {
  margin-top: 0px;
}

.fc-toolbar {
  margin: 0px;
  padding: 24px 0px;
}

.hide {
  display: none;
}

// new
.fc td,
.fc th {
  text-align: center;
}

.fc-ltr .fc-basic-view .fc-day-top .fc-day-number {
  float: unset;
}

tr:first-child > td > .fc-day-grid-event {
  margin: 5px;
}

.fc-day-grid-event .fc-time {
  font-weight: 500;
}

.hide {
  display: none !important;
}

.calendar-events {
  cursor: move;
}
