//
// Global style
//
* {
  outline: none;
}

body {
  overflow-x: hidden;
  margin: 0;
  color: $body-color;
  background: $body-bg;
  position: relative;
}

html {
  position: relative;
  min-height: 100%;
}

b,
strong {
  font-weight: $font-weight-medium; // Add the correct font weight in Chrome, Edge, and Safari
}

a {
  color: $themecolor;
  text-decoration: none;
  &.hover-primary {
    &:hover {
      color: $primary !important;
    }
  }
  &:hover,
  &:focus {
    text-decoration: none;
    color: $primary;
  }

  &.link,
  &.page-link {
    color: $body-color;

    &:hover,
    &:focus {
      color: $themecolor;
    }
  }
}

// Opacity
.op-5 {
  opacity: 0.5;
}

//
// Fonts setting
//
html body {
  .font-weight-medium {
    font-weight: $font-weight-medium;
  }

  .display-5 {
    font-size: 3rem;
  }
  .display-6 {
    font-size: 2.5rem;
  }
  .display-7 {
    font-size: 2rem;
  }
}

//
// List Style none
//
ul.list-style-none {
  margin: 0;
  padding: 0;

  li {
    list-style: none;
    a {
      display: block;
      padding: 8px 0;
      color: $body-color;
      text-decoration: none;
      &:hover {
        color: $themecolor;
      }
    }
  }
}

//
// Rounds
//
.round {
  line-height: 45px;
  width: 45px;
  height: 45px;
}

.round-md {
  line-height: 35px;
  width: 35px;
  height: 35px;
}

.round-lg {
  line-height: 65px;
  width: 60px;
  height: 60px;
  font-size: 30px;
}

//
// List of icons
//
ul.list-icons {
  li {
    a {
      color: $body-color;
      &:hover {
        color: $info;
      }
    }
    i {
      font-size: 13px;
    }
  }
}

// Custom select
.custom-select {
  -moz-appearance: none;
  -webkit-appearance: none;
  -o-appearance: none;
  background-size: auto;
}

//
// Table
//
.no-wrap {
  td,
  th {
    white-space: nowrap;
  }
}

.v-middle {
  td,
  th {
    vertical-align: middle;
  }
}

//
// Grids
.grid-structure .grid-container {
  background-color: $light;
  margin-bottom: 10px;
  font-size: 0.8rem;
  padding: 10px 20px;
}

// ----------------------------------------------
// Icon page
// ----------------------------------------------
.icon-hover {
  cursor: pointer;
  border-radius: 8px;
  i {
    font-size: 20px;
    margin-right: 8px;
  }
  &:hover {
    background-color: $gray-200;
    color: $primary;
  }
}

// form select
.form-select {
  background-image: url(../../assets/images/custom-select.png);
  background-size: auto;
}

.theme-select {
  background-color: $gray-300;
  color: $text-muted;
  width: auto;
  padding: 9px 33px 9px 16px;
}

// custompadding class
.p-05 {
  padding: 5px;
}
.p-10 {
  padding: 10px;
}
.p-20 {
  padding: 20px;
}
.p-30 {
  padding: 30px;
}

.table.varient-table {
  th {
    color: $table-th-color !important;
  }
  tr {
    &:last-child {
      td {
        border-bottom-width: 0;
      }
    }
  }
}

.text-muted-lite {
  color: $table-th-color !important;
}

@include media-breakpoint-down(sm) {
  .text-sm-nowrap {
    white-space: nowrap;
  }
}

.or-divider {
  position: relative;
  &:before {
    content: "";
    background: $border-color;
    height: 2px;
    width: 100%;
    position: absolute;
    left: 0;
    top: 10px;
  }
}
