//
// Button lists margin
//
.button-group {
  .btn {
    margin-bottom: 8px;
    margin-right: 8px;
  }
}
.btn-rounded-lg {
  border-radius: 10px;
}

.btn {
  box-shadow: none;
}

//
// Circle Buttons
//
// There are mixins which we have created in mixins/mix_buttons

.btn-circle {
  border-radius: 100%;
  width: 45px;
  height: 45px;
  padding: 5px;
  line-height: 30px;

  // Different Size of Buttons
  // #####################################################################
  &.btn-sm {
    width: 30px;
    height: 30px;
    padding: 8px 10px;
    font-size: 14px;
  }
  &.btn-md {
    width: 35px;
    height: 35px;
    padding: 8px 10px;
    font-size: 14px;
  }
  &.btn-lg {
    width: 48px;
    height: 48px;
    padding: 14px 10px;
    font-size: 18px;
    line-height: 23px;
  }
  &.btn-xl {
    width: 60px;
    height: 60px;
    padding: 14px 15px;
    font-size: 24px;
  }
}

//
// Rounded Buttons
//
// There are mixins which we have created in mixins/mix_buttons

.btn-rounded {
  border-radius: 60px;
  padding: 7px 18px;

  // Different Size of Buttons
  // #####################################################################
  &.btn-xs {
    padding: 0.25rem 0.5rem;
    font-size: 10px;
  }
  &.btn-sm {
    padding: 0.25rem 0.5rem;
    font-size: 12px;
  }
  &.btn-md {
    padding: 12px 35px;
    font-size: 16px;
  }
  &.btn-lg {
    padding: 0.75rem 1.5rem;
  }
}

//
// Extra Small Button
//
.btn-xs {
  padding: 0.25rem 0.5rem;
  font-size: 10px;
}

//
// Active with icons
//
.btn {
  .text-active {
    display: none;
  }
  &.active {
    .text-active {
      display: inline-block;
    }
  }
}

// ------------------------------------------
// Buttons
// ------------------------------------------
button:focus {
  outline: 0;
}

.btn-danger,
.btn-primary,
.btn-info,
.btn-warning,
.btn-success {
  color: $white;
  &:hover,
  &:focus,
  &.disabled,
  &:disabled,
  &.active {
    color: $white;
  }
}

.btn-outline-danger,
.btn-outline-primary,
.btn-outline-info,
.btn-outline-warning,
.btn-outline-success {
  &:hover {
    color: $white;
  }
}

.btn-check {
  &:checked,
  &.active {
    & + .btn-outline-danger,
    & + .btn-outline-primary,
    & + .btn-outline-info,
    & + .btn-outline-warning,
    & + .btn-outline-success {
      color: $white;
    }
  }
}

.btn-xs {
  padding: 0.25rem 0.5rem;
  font-size: 10px;
}

.btn-rounded {
  border-radius: 60px;
  padding: 7px 18px;

  &.btn-lg {
    padding: 0.75rem 1.5rem;
  }
  &.btn-sm {
    padding: 0.25rem 0.5rem;
    font-size: 12px;
  }
  &.btn-xs {
    padding: 0.25rem 0.5rem;
    font-size: 10px;
  }
  &.btn-md {
    padding: 12px 35px;
    font-size: 16px;
  }
}

.btn-default {
  background-color: $info;
  color: $white;
  &:hover {
    color: $white;
  }
}

